function getGallerySettings() {
  let settings = [];
  for (let i of urls) {
    let img = new Image();
    img.src = i;
    img.onload = () =>
      settings.push({
        src: img.src,
        height: img.height,
        width: img.width,
      });
  }
  return settings;
}

